import Vue from 'vue'
import Router from 'vue-router'

import Home from '../components/pages/home/home.vue'
import History from '../components/pages/history/history.vue'
import Purpose from '../components/pages/purpose/purpose.vue'
import Mine from '../components/pages/mine/mine.vue'

import Hot from '../components/pages/purpose/hot.vue'
import Country from '../components/pages/purpose/country.vue'
import  Forign from '../components/pages/purpose/forign.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {path:'/home',component:Home},
    {path:'/history',component:History},
    {path:'/purpose',component:Purpose,children:[
    {path:'/purpose/hot',component:Hot},
    {path:'/purpose/country',component:Country},
    {path:'/purpose/forign',component:Forign}
]},
    {path:'/mine',component:Mine}
  ]
})



