<!---->
<template>
  <div class="tabbar-item"  @click="fn(index)">
    <span v-show="iconShow"><slot name="normalIcon"></slot></span>
    <span v-show="!iconShow"><slot name="activeIcon"></slot></span>
    <p v-text="text" :class="{active:!iconShow}"></p>
  </div>
</template>
<script>
 export default {
   data(){
       return {
       }
   },
   props: ['text','index','selected'],
   methods: {
     fn: function(index){
//      index
       this.$emit('clickIndex',index);
     },
//    computeddata 
//    computed 
   },
   computed: {
     iconShow:function () {
       if(this.selected ==this.index) {
         return false
       }
       return true
     }
     }
 }
</script>
<style>
  .tabbar-item{
    width: 25%;
    height:4rem;
    text-align: center;
    float: left;
  }
  .tabbar-item img{
    width: 50%;
    height:2.5rem;
    transform:scale(.7);
  }
  .tabbar-item p{
    font-size:0.65rem;
    vertical-align:top;
    color: #919191;
  }
  .tabbar-item .active{
    color: #1448ae;
  }
</style>



