<template>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <img src="./../../../assets/shouye-lunbo/banner1.jpg" alt="">
        </div>
        <div class="swiper-slide">
          <img src="./../../../assets/shouye-lunbo/banner2.jpg" alt="">
        </div>
        <div class="swiper-slide">
          <img src="./../../../assets/shouye-lunbo/banner3.jpg" alt="">
        </div>
        <div class="swiper-slide">
          <img src="./../../../assets/shouye-lunbo/banner4.jpg" alt="">
        </div>
      </div>
      <!--  -->
      <div class="swiper-pagination"></div>
    </div>
</template>
<script>
  import Swiper from './../../../../node_modules/swiper/dist/js/swiper.min'
  export default{
      mouted:function(){
        new Swiper('.swiper-container',{
          autoplay:true,
          grabCursor : true,
          loop : true,
          // 
          pagination: '.swiper-pagination',
          // 
        })
      }
  }
</script>
<style>
  @import './../../../../node_modules/swiper/dist/css/swiper.min.css';
  .swiper-container{
    width:100%;
    max-width:18.75rem;
    /*min-width:8rem;*/
    height:11.5rem;
    margin:0 auto;

  }
</style>



