<!---->
<template>
 <div class="tabbar-box">
   <router-link to="/home">
     <item text="" @clickIndex="getIndex" index ="home" :selected="selected">
       <img src="../assets/icon/footer_0322_05.png" slot="normalIcon">
       <img src="../assets/icon/footer_0322_01.png" slot="activeIcon">
     </item>
   </router-link>
   <router-link to="/purpose" >
     <item text="" @clickIndex="getIndex" index="purpose" :selected="selected">
       <img src="../assets/icon/footer_0322_06.png" slot="normalIcon">
       <img src="../assets/icon/footer_0322_02.png" slot="activeIcon">
     </item>
   </router-link>
   <router-link to="/history" >
     <item text="" @clickIndex="getIndex" index="history" :selected="selected">
       <img src="../assets/icon/footer_0322_07.png" slot="normalIcon">
       <img src="../assets/icon/footer_0322_03.png" slot="activeIcon">
     </item>
   </router-link>
   <router-link to="/mine" >
     <item text="" @clickIndex="getIndex" index="mine" :selected="selected">
       <img src="../assets/icon/footer_0322_08.png" slot="normalIcon">
       <img src="../assets/icon/footer_0322_04.png" slot="activeIcon">
   </item>
   </router-link>

 </div>
</template>
<script>
  import item from  './tabbar-ietm.vue'
  export default {
    data(){
      return{
//     selected
        selected:''
      }
    },
    components:{
      item
    },
    methods:{
      getIndex:function(val){
        this.selected=val;
      }
    },
    created:function(){
      var hash= window.location.hash.split('/')[1];
      if( hash=='#'){
        this.selected='#/home';
        return;
      }
      this.selected=hash;
    }


  }
</script>
<style>
  .tabbar-box{
    width: 100%;
    height:3.5rem;
    position:fixed;
    bottom:0;
    left:0;
    background-color: #ededed;
    z-index:999;
  }
</style>



