
<template>
  <div>
    <div class="purpose-top">
    <div class="purpose-top-content">
    <i></i>
    <p></p>
    <div class="purpose-inp">
    <input type="text" placeholder="" >
    <span></span>
    </div>
    </div>
    </div>
    <div class="nbsp"></div>
    <div class="hot">
      <ul class="left">
        <router-link :to="item.tiaoZhuan" v-for="item in arr">
          <li :class="{actives:item.tiaoZhuan==selected}" @click="clickItem(item.tiaoZhuan)">
            <span v-show="item.tiaoZhuan!=selected">
                <img src="" alt="" :src="item.iconPlace">
            </span>
            <span v-show="item.tiaoZhuan==selected">
              <img src="" alt="" :src="item.iconActive">
            </span>
            <p v-text="item.hot"></p>
          </li>
        </router-link>
      </ul>
      <div class="hot-right">
        <router-view></router-view>
      </div>
    </div>

  </div>
</template>

<script>
import purpose from './../../tobBar.vue'
  export  default {
components: {
purpose
  },
    data() {
      return {
        text: '',
        selected:'',
        show:true,
        arr: [
          {
            tiaoZhuan:'/purpose/hot',
            hot: '',
            iconPlace: "./src/assets/icon/icon-place-hot.png",
            iconActive: "./src/assets/icon/icon-place-active-hot.png"
          },
          {
            tiaoZhuan:"/purpose/country",
            hot:'',
            iconPlace:"./src/assets/icon/icon-place-inland.png",
            iconActive:"./src/assets/icon/icon-place-active-inland.png"
          },
          {
            tiaoZhuan:'/purpose/forign',
            hot:'',
            iconPlace:"./src/assets/icon/icon-place-foreign.png",
            iconActive:"./src/assets/icon/icon-place-active-foreign.png"
          }
        ]
      }
    },
    beforeCreate:function () {
      this.$router.push('/purpose/hot')
    },
    methods:{
      clickItem:function (tiaoZhuan) {
        this.selected=tiaoZhuan;
      }
    },
    created:function () {
      var hash=window.location.hash.substr(1);
      if(hash=='/hot'){
        this.selected = '/purpose/hot';
        return;
      }
      this.selected = hash;
    }
  }
</script>

<style>
  *{
    margin:0;
  padding:0;
  }
  a{
    text-decoration: none;
  }
  .purpose-top{
      width: 100%;
      height:4.375rem;
      background-color: #1448ae;
      display:flex;
      justify-content:space-between;
      position: fixed;
      top:0;
      left:0;
      z-index:9999;
      }
  .nbsp{
     width: 100%;
   height:4.375rem;
    }
.purpose-top-content{
  width:90%;
  height:4.375rem;
      margin:0 auto;
    }
.purpose-top-content p{
      margin-top:0.6rem;
      text-align: center;
      font-size:0.85rem;
      color: #fff;
      /*!*margin-bottom:0.5rem;*!-->*/

      }
  .purpose-top-content>i{
      display: block;
      width:0.5rem;
      height:0.85rem;
      background:url("../../../assets/icon/purposelogin_03.jpg");
      background-size:cover;
      position: absolute;
      top:0.6rem;
      left:2rem;
      }
 .purpose-inp{
      width: 90%;
      height:1.575rem;
      margin:0 auto;
      margin-top:0.5rem;
      position: relative;
      }
  .purpose-inp input{
      width: 100%;
      height: 1.575rem;
      border-radius:30px;
      outline-style:none;
      border: 1px solid #8aa4d7;
      text-indent:1.5rem;
      background-color:transparent;
      }
  .purpose-inp span{
      position: absolute;
      top:0.295rem;
      left:0.35rem;
      width: 0.85rem;
      height:0.85rem;
      background:url("../../../assets/icon/galss_03.png") no-repeat ;
      background-size:cover;
      }
  .purpose-inp input::-webkit-input-placeholder {
      color:#bfc6c3;
      font-size:0.65rem;
      }
  .hot {
    width:95%;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
  }

  .hot > .left {
    width: 6.4rem;
    height: 63rem;
    /*background-color: #F4F4F4;*/
    padding-left: 0;
  }

  .hot .left li span {
    display: inline-block;
    width: 27px;
    height: 24px;
    margin-bottom: 5px;
  }

  .hot .left li span img {
    width: 100%;
  }

  .hot .left li {
    width: 3.34rem;
    height: 6.46rem;
    background-color: #F4F4F4;
    color: #8C8C8C;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-left: 4px solid transparent;
  }
  .hot .left a {
    text-decoration: none;
  }
  .hot .left .actives {
    border-left: 4px solid #F7C612;
    background-color: #fff;
    color: #0449C0;
  }

  .hot-right {
    width: 100%;
    height: 100%;
    padding-right: 10px;
    padding-top: 10px;
  }

  ul {
    list-style: none;
  }

  .search {
    width: 100%;
    /*height: 40px;*/
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .5);
  }

  .search .search-input {
    width: 95%;
    height: 3rem;
    margin: 5px auto;
    position: relative;
  }

  .search .search-input::before {
    content: '';
    position: absolute;
    top: -2px;
    left: 0;
    width: 3.5rem;
    height: 3.5rem;
    background: url("../../../assets/icon/galss_03.png") no-repeat;
    transform: scale(0.6);

  }

  .search .search-input input {
    width: 100%;
    height: 100%;
    line-height: 3rem;
    font-size: 1.6rem;
    border: 1px solid #fff;
    color: #fff;
    outline: none;
    border-radius: 1.5rem;
    text-indent: 5rem;
    background-color: transparent;
  }

  .search .search-input input::-webkit-input-placeholder {
    color: #fff;
    font-size: 1.6rem;
  }

  .search ul {
    padding-left: 0.5rem;
    display: flex;
    align-items: center;
    /*background-color: blue;*/
  }

  .search ul li {
    float: left;
  }

  .search > ul .img {
    width: 2.5rem;
    height: 3.5rem;
    background: url("../../../assets/icon/sprites.png") no-repeat -102px -33px;
    transform: scale(0.6);
  }

  .search > ul li:nth-of-type(2) {
    margin: 0 auto;
    color: white;
    font-size: 1.8rem;
  }

  .search > ul li:nth-of-type(3) {
    padding-right: 1rem;
    color: white;
  }
  .col-md-6 {
    margin-bottom: 10px;
    padding-right: 0;
  }
  .col-md-6 .border img{
    width: 100%;
  }
  .col-md-6 .border p{
    text-align: center ;
    margin-top: 2px;
  }
  .border{
    border: 1px solid #dcdcdc;
  }
</style>



